<template>
  <v-container>
    <h1>Ë®≠ÂÆöÁîªÈù¢</h1>
    <v-file-input
      v-model="file"
      label="CSV„Éï„Ç°„Ç§„É´„Çí„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ"
      accept=".csv"
      @change="handleFileUpload"
    ></v-file-input>
    <v-btn @click="saveSentences" color="primary" :disabled="!file">
      ‰øùÂ≠ò
    </v-btn>
    <v-alert v-if="message" :type="messageType" class="mt-4">
      {{ message }}
    </v-alert>
  </v-container>
</template>

<script>
export default {
  name: 'SettingsScreen',
  data() {
    return {
      file: null,
      sentences: [],
      message: '',
      messageType: 'info'
    }
  },
  methods: {
    handleFileUpload() {
      if (this.file) {
        const reader = new FileReader()
        reader.onload = (e) => {
          const content = e.target.result
          this.sentences = content.split('\n').filter(line => line.trim() !== '')
        }
        reader.readAsText(this.file)
      }
    },
    saveSentences() {
      if (this.sentences.length > 0) {
        // „Åì„Åì„Åß„Çª„É≥„ÉÜ„É≥„Çπ„Çí‰øùÂ≠ò„Åô„ÇãÂá¶ÁêÜ„ÇíÂÆüË£Ö
        // ‰æã: localStorage.setItem('sentences', JSON.stringify(this.sentences))
        console.log('‰øùÂ≠ò„Åï„Çå„ÅüÊñáÁ´†:', this.sentences)
        this.message = 'ÊñáÁ´†„ÅåÊ≠£Â∏∏„Å´‰øùÂ≠ò„Åï„Çå„Åæ„Åó„Åü„ÄÇ'
        this.messageType = 'success'
      } else {
        this.message = '‰øùÂ≠ò„Åô„ÇãÊñáÁ´†„Åå„ÅÇ„Çä„Åæ„Åõ„Çì„ÄÇ'
        this.messageType = 'error'
      }
    }
  }
}
</script>
